@import url(https://fonts.googleapis.com/css2?family=Grechen+Fuemen&family=Roboto:wght@100&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.app{
    display: flex;
    flex-direction: column;
    background-color:#56669d;
    justify-content: center;
    text-align: center;
}
.app__searchForm{
    margin-top: 18px;
    
    
}

.app__input{
    padding: 6px 16px;
    min-width: 200px;
    max-width: 100vw;
    font-size: 15px;
}
.app__submit{
    padding: 6px 16px;
    font-size: 15px;
    margin-left: 6px;
    background-color:#fa9490 ;
    border: 2px solid#fa9490;
    border-radius: 4px;
    color: white;
}
.app__recipes{
     padding: 6px 16px;
    display: grid;
    justify-content: center;
    margin-top: 24px;
    grid-template-columns: 230px 230px;
    background-color: #fa9490;
    
}
.recipeTile__img{
    height: 200px;
    width: 200px;
    object-fit: cover;
    border-radius: 4px;
}

.recipeTile__name{
    max-width: 200px;
    text-align: center;
    margin: 12px 0px;
}
